/* eslint-disable eqeqeq */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useState, useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import Select from 'react-select';
// import DateRangePicker from 'react-daterange-picker';
import { DateRangePicker } from 'react-dates';
import 'react-dates/initialize';
import 'react-daterange-picker/dist/css/react-calendar.css';
import 'react-dates/lib/css/_datepicker.css';
import { Formik } from 'formik';
import * as Yup from 'yup';
import makeAnimated from 'react-select/animated';
import moment from 'moment';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import {
  addPromocodeSaga,
  getAllRestaurantSaga,
  getPromocodeDetailByIdSaga,
  updatePromocodeSaga,
  resetPromocodeDetail,
} from '../../../store/actions';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import {
  MINIMUM_CART_REQUIRED,
  MINIMUM_CART_NUMBER,
  MAXIMUM_DISCOUNT_REQUIRED,
  MAXIMUM_DISCOUNT_NUMBER,
  COUPON_CODE_REQUIRED,
  COUPON_PERCENTAGE_REQUIRED,
  COUPON_PERCENTAGE_NUMBER,
  SELECT_RESTAURANT,
  SELECT_DATE_RANGE,
} from '../../../constants/errorConstants';

const animatedComponents = makeAnimated();

const AddPromocode = props => {
  const editId = props.match.params.id;
  const [isDisable, setIsDisable] = useState(false);
  const [isEditValueUpdated, setIsEditValueUpdated] = useState(false);
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();
  const [focusedInput, setFocusedInput] = useState('');
  const [photo, setPhoto] = useState('');
  const [photoURL, setPhotoURL] = useState('');
  // const [dateRangeValue, setDateRangeValue] = useState(null);
  const [dateRangeValueHasError, setDateRangeValueHasError] = useState(null);
  const couponTypeOptions = [
    { id: 1, value: 'Flat', label: 'Flat' },
    { id: 2, value: 'Percentage', label: 'Percentage' },
  ];
  const [couponTypeSelected, setCouponTypeSelected] = useState(
    couponTypeOptions[0],
  );
  const [couponType, setCouponType] = useState('Flat');
  const [selectedRestaurants, setSelectedRestaurants] = useState([]);
  const [selectedRestaurantsOld, setSelectedRestaurantsOld] = useState([]);
  const [
    selectedRestaurantsHasError,
    handleSelectedRestaurantsHasError,
  ] = useState(false);
  const [couponStatus, setCouponStatus] = useState(true);
  const toggleCouponStatus = useCallback(() => setCouponStatus(!couponStatus));
  const [allRestaurant, setAllRestaurant] = useState(false);
  const toggleAllRestaurant = useCallback(() =>
    setAllRestaurant(!allRestaurant),
  );
  // const [isToggledDatePicker, setIsToggled] = useState(false);
  // const toggleDatePicker = useCallback(() =>
  //   setIsToggled(!isToggledDatePicker),
  // );
  const [initialValuesFlat, setInitialValuesFlat] = useState({
    minimumCartValue: '',
    maxDiscount: '',
    couponCode: '',
  });
  const [initialValuesPercentage, setInitialValuesPercentage] = useState({
    minimumCartValue: '',
    maxDiscount: '',
    couponPercentage: '',
    couponCode: '',
  });
  const {
    restaurants: restaurantsOptions,
    promocodeDetail,
    // eslint-disable-next-line no-unused-vars
    errorMsgAddPromoCode,
  } = useSelector(state => state.promocode);
  const { open } = useSelector(state => state.modal);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllRestaurantSaga());
    if (editId) {
      dispatch(getPromocodeDetailByIdSaga({ id: editId }));
    }
    return () => {
      dispatch(resetPromocodeDetail());
    };
  }, []);

  if (promocodeDetail._id !== undefined) {
    if (!isEditValueUpdated) {
      // setDateRangeValue(
      //   moment.range(
      //     moment(promocodeDetail.startDate),
      //     moment(promocodeDetail.endDate),
      //   ),
      // );
      setStartDate(moment(promocodeDetail.startDate));
      setEndDate(moment(promocodeDetail.endDate));
      setCouponStatus(promocodeDetail.status);
      setAllRestaurant(promocodeDetail.forAllChef);
      setSelectedRestaurantsOld(promocodeDetail.chefIds);
      setSelectedRestaurants(promocodeDetail.chefIds);
      if (promocodeDetail.thumbnail !== null) {
        setPhoto(promocodeDetail.thumbnail);
      }
      let values = {};
      if (promocodeDetail.isFlat) {
        setCouponTypeSelected(couponTypeOptions[0]);
        values = {
          minimumCartValue: promocodeDetail.minimumCartValue,
          maxDiscount: promocodeDetail.maxDiscount,
          couponCode: promocodeDetail.code,
        };
      } else {
        setCouponType('Percentage');
        setCouponTypeSelected(couponTypeOptions[1]);
        values = {
          minimumCartValue: promocodeDetail.minimumCartValue,
          maxDiscount: promocodeDetail.maxDiscount,
          couponCode: promocodeDetail.code,
          couponPercentage: promocodeDetail.percentage,
        };
        setInitialValuesFlat(values);
      }
      setIsEditValueUpdated(true);
      setInitialValuesFlat(values);
      setInitialValuesPercentage(values);
    }
  }

  const validationSchemaFlat = Yup.object({
    minimumCartValue: Yup.number()
      .typeError(MINIMUM_CART_NUMBER)
      .required(MINIMUM_CART_REQUIRED),
    maxDiscount: Yup.number()
      .typeError(MAXIMUM_DISCOUNT_NUMBER)
      .required(MAXIMUM_DISCOUNT_REQUIRED),
    couponCode: Yup.string().trim().required(COUPON_CODE_REQUIRED),
  });

  const validationSchemaPercentage = Yup.object({
    minimumCartValue: Yup.number()
      .typeError(MINIMUM_CART_NUMBER)
      .required(MINIMUM_CART_REQUIRED),
    maxDiscount: Yup.number()
      .typeError(MAXIMUM_DISCOUNT_NUMBER)
      .required(MAXIMUM_DISCOUNT_REQUIRED),
    couponPercentage: Yup.number()
      .max(100)
      .typeError(COUPON_PERCENTAGE_NUMBER)
      .required(COUPON_PERCENTAGE_REQUIRED),
    couponCode: Yup.string().trim().required(COUPON_CODE_REQUIRED),
  });

  const getImage = evt => {
    const { files } = evt.target;
    if (files[0]) {
      if (files[0].type.includes('image')) {
        const filename = files[0].name;
        const fileExtension = filename.substr(filename.lastIndexOf('.') + 1);
        if (
          fileExtension.toLowerCase() === 'png' ||
          fileExtension.toLowerCase() === 'jpg' ||
          fileExtension.toLowerCase() === 'gif' ||
          fileExtension.toLowerCase() === 'jpeg'
        ) {
          setPhoto(files[0]);
          setPhotoURL(URL.createObjectURL(files[0]));
        }
      }
    }
  };

  // const handleSelectDateRange = range => {
  //   // range is a moment-range object
  //   setDateRangeValue(range);
  //   if (range) {
  //     setDateRangeValueHasError(false);
  //   }
  //   toggleDatePicker();
  // };

  const onFocusChangeRangeHandler = focusedInputValue => {
    setFocusedInput(focusedInputValue);
  };

  const rangeDatesChangeHandler = event => {
    setStartDate(event.startDate);
    setEndDate(event.endDate);
    if (event.startDate && event.endDate) {
      setDateRangeValueHasError(false);
    }
    // To enable range
    // if (moment(event.endDate).endOf('day').isBefore(moment())) {
    //   setEndDate(null);
    // }
  };

  const handleCouponType = value => {
    setCouponType(value.value);
    setCouponTypeSelected(value);
  };

  const handleSave = values => {
    let isError = false;
    const chefIds = [];
    const requestBody = {};
    if (!allRestaurant) {
      if (selectedRestaurants.length > 0) {
        selectedRestaurants.forEach(value => {
          chefIds.push(value.id);
        });
        requestBody.chefIds = chefIds;
        requestBody.forAllChef = false;
        handleSelectedRestaurantsHasError(false);
      } else {
        isError = true;
        handleSelectedRestaurantsHasError(true);
      }
    } else {
      requestBody.forAllChef = true;
    }

    if (couponType !== 'Flat') {
      requestBody.percentage = values.couponPercentage;
    }

    if (photo) {
      requestBody.photo = photo;
    }

    if (startDate && endDate) {
      requestBody.startDate = moment(startDate).format('yyyy-MM-DD');
      requestBody.endDate = moment(endDate).format('yyyy-MM-DD');
    } else {
      isError = true;
      setDateRangeValueHasError(true);
    }

    requestBody.isFlat = couponType === 'Flat';
    requestBody.minimumCartValue = values.minimumCartValue;
    requestBody.maxDiscount = values.maxDiscount;
    requestBody.code = values.couponCode;
    requestBody.status = couponStatus;

    if (!isError) {
      setIsDisable(true);
      if (editId) {
        requestBody.id = editId;

        if (!allRestaurant) {
          const addedChefs = [];
          const deletedChefs = [];

          // storing newly added Vendors
          selectedRestaurants.forEach(chef => {
            let isAvailableAlready = false;
            selectedRestaurantsOld.forEach(oldChef => {
              if (chef.id === oldChef.id) {
                isAvailableAlready = true;
              }
            });
            if (!isAvailableAlready) {
              addedChefs.push(chef.id);
            }
          });

          // storing removed chefs from old chef's lists
          selectedRestaurantsOld.forEach(oldChef => {
            let isRemoved = true;
            selectedRestaurants.forEach(chef => {
              if (oldChef.id === chef.id) {
                isRemoved = false;
              }
            });
            if (isRemoved) {
              deletedChefs.push(oldChef.id);
            }
          });

          if (addedChefs.length > 0) {
            requestBody.addedChefIds = addedChefs;
          }
          if (deletedChefs.length > 0) {
            requestBody.deletedChefIds = deletedChefs;
          }
        }

        dispatch(updatePromocodeSaga({ requestBody, setIsDisable }));
      } else {
        dispatch(addPromocodeSaga({ requestBody, setIsDisable }));
      }
    }
  };

  return (
    <CardWithBack
      pageTitle={
        props.history.location.pathname.includes('add')
          ? 'Add Promocode'
          : 'Edit Promocode'
      }
      dataLoadeed={typeof 1 === typeof 2}
      errorMsgAddPromoCode=""
    >
      <Formik
        enableReinitialize
        initialValues={
          couponType === 'Flat' ? initialValuesFlat : initialValuesPercentage
        }
        validationSchema={
          couponType === 'Flat'
            ? validationSchemaFlat
            : validationSchemaPercentage
        }
        onSubmit={(values, { setSubmitting }) => {
          handleSave(values);
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          // isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className="row">
              <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                <div className="form-group field-coupon-coupon_minimum_cart_value required">
                  <label
                    htmlFor="coupon-coupon_minimum_cart_value"
                    className={editId ? '' : 'required-field'}
                  >
                    Coupon Type
                  </label>
                  <Select
                    defaultValue={couponTypeOptions[0]}
                    value={couponTypeSelected}
                    options={couponTypeOptions}
                    onChange={handleCouponType}
                    isDisabled={!!editId}
                  />
                </div>
              </div>

              <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                <div className="form-group field-coupon-coupon_minimum_cart_value required">
                  <label
                    htmlFor="coupon-coupon_minimum_cart_value"
                    className="required-field"
                  >
                    Minimum Cart Value
                  </label>
                  <input
                    type="text"
                    id="coupon-coupon_minimum_cart_value"
                    className="form-control"
                    name="minimumCartValue"
                    value={values.minimumCartValue}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    aria-required="true"
                  />
                  <div className="error-message">
                    {errors.minimumCartValue &&
                      touched.minimumCartValue &&
                      errors.minimumCartValue}
                  </div>
                </div>
              </div>

              <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                <div className="form-group field-coupon-coupon_value required">
                  <label
                    htmlFor="coupon-coupon_value"
                    className="required-field"
                  >
                    Max Discount
                  </label>
                  <input
                    type="text"
                    id="coupon-coupon_value"
                    className="form-control"
                    name="maxDiscount"
                    // disabled={!!editId}
                    value={values.maxDiscount}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    aria-required="true"
                  />
                  <div className="error-message">
                    {errors.maxDiscount &&
                      touched.maxDiscount &&
                      errors.maxDiscount}
                  </div>
                </div>
              </div>

              {couponType === 'Percentage' ? (
                <div>
                  <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                    <div className="form-group field-coupon-coupon_value required">
                      <label
                        htmlFor="coupon-coupon_percentage"
                        className="required-field"
                      >
                        Percentage
                      </label>
                      <input
                        type="text"
                        id="coupon-coupon_percentage"
                        className="form-control"
                        name="couponPercentage"
                        disabled={!!editId}
                        value={values.couponPercentage}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        aria-required="true"
                      />
                      <div className="error-message">
                        {errors.couponPercentage &&
                          touched.couponPercentage &&
                          errors.couponPercentage}
                      </div>
                    </div>
                  </div>
                </div>
              ) : (
                ''
              )}

              <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                <div className="form-group field-coupon-coupon_code">
                  <label
                    htmlFor="coupon-coupon_code"
                    className={editId ? '' : 'required-field'}
                  >
                    Coupon Code
                  </label>
                  <input
                    type="text"
                    id="coupon-coupon_code"
                    className="form-control"
                    name="couponCode"
                    disabled={!!editId}
                    value={values.couponCode}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <div className="error-message">
                    {errors.couponCode &&
                      touched.couponCode &&
                      errors.couponCode}
                  </div>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                <h4 className="required-field">Valid To - From:</h4>
                <div className="dateRangePickerWrapper">
                  <DateRangePicker
                    readOnly
                    small
                    minDate={moment().add(1, 'day')}
                    displayFormat={() => 'DD/MM/yyyy'}
                    startDatePlaceholderText="Start Date"
                    endDatePlaceholderText="End Date"
                    startDateId="startDateId"
                    endDateId="endDateId"
                    startDate={startDate}
                    endDate={endDate}
                    onDatesChange={rangeDatesChangeHandler}
                    focusedInput={focusedInput}
                    disabled={
                      moment(startDate).endOf('day').isBefore(moment())
                        ? 'startDate'
                        : ''
                    }
                    // disabled
                    onFocusChange={onFocusChangeRangeHandler}
                    numberOfMonths={1}
                    // isOutsideRange={() => false}
                    isOutsideRange={date => {
                      if (date > moment().startOf('day').add('1', 'day')) {
                        return false;
                      }
                      return true;
                    }}
                  />
                </div>

                {/* <div className="dateRangePickerWrapper">
                  <div className="row">
                    <div className="col-sm-9 col-md-9 col-lg-6">
                      <div className="mb-10" id="data_5">
                        {isToggledDatePicker ? (
                          <>
                            <div
                              className="modal-backdrop fade in date-backdrop"
                              onClick={() => {
                                toggleDatePicker();
                              }}
                            />
                            <DateRangePicker
                              initialRange={(moment(), moment().add(2, 'days'))}
                              minimumDate={moment().add(1, 'day')}
                              value={dateRangeValue}
                              onSelect={handleSelectDateRange}
                            />
                          </>
                        ) : (
                          ''
                        )}
                        <div
                          className="input-daterange input-group"
                          id="datepicker"
                        >
                          <span className="input-group-addon">
                            <i className="fa fa-calendar" />
                          </span>
                          <input
                            type="text"
                            placeholder="Start date"
                            className="input-sm form-control"
                            name="start"
                            value={
                              dateRangeValue &&
                              dateRangeValue.start != null &&
                              dateRangeValue.start.format('DD/MM/yyyy')
                            }
                            onClick={e => {
                              toggleDatePicker();
                              e.target.blur();
                            }}
                          />
                          <span className="input-group-addon bor-left">to</span>
                          <span className="input-group-addon">
                            <i className="fa fa-calendar" />
                          </span>
                          <input
                            type="text"
                            placeholder="End date"
                            className="input-sm form-control"
                            name="end"
                            value={
                              dateRangeValue &&
                              dateRangeValue.end != null &&
                              dateRangeValue.end.format('DD/MM/yyyy')
                            }
                            onClick={e => {
                              toggleDatePicker();
                              e.target.blur();
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div> */}
                {dateRangeValueHasError ? (
                  <div className="error-message date-picker-error">
                    {SELECT_DATE_RANGE}
                  </div>
                ) : (
                  ''
                )}
              </div>
            </div>
            <div
              style={{ marginBottom: '10px' }}
              className="input-group form-group  field-coupon-coupon_status required success 
              mt-coupon-status"
            >
              <label style={{ marginBottom: '5px' }} className="required-field">
                Coupon Status
              </label>
              <input type="hidden" name="Coupon[coupon_status]" value="" />
              <div
                id="coupon-coupon_status"
                role="radiogroup"
                aria-required="true"
                aria-invalid="false"
              >
                <label className="switch">
                  <input
                    type="checkbox"
                    checked={couponStatus}
                    onChange={toggleCouponStatus}
                  />
                  <span className="slider round" />
                </label>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12 col-md-12 col-lg-12">
                <div className=" text-left">
                  <div className="form-group">
                    <h4 className="required-field">Select Restaurants</h4>

                    <input
                      type="checkbox"
                      id="selectAllRestaurant"
                      name="selectAllRestaurant"
                      value="allrestaurant"
                      checked={allRestaurant}
                      onChange={toggleAllRestaurant}
                      style={{ marginRight: '5px' }}
                    />
                    <label
                      htmlFor="selectAllRestaurant"
                      style={{
                        fontSize: '14px',
                        lineHeight: '14px',
                        marginBottom: '10px',
                      }}
                    >
                      Select All Restaurants
                    </label>
                    {!allRestaurant ? (
                      <div>
                        <div className="">
                          <Select
                            placeholder="Select Restaurants"
                            closeMenuOnSelect={false}
                            components={animatedComponents}
                            value={selectedRestaurants}
                            isMulti
                            options={restaurantsOptions}
                            onChange={restaurants => {
                              if (
                                restaurants != null &&
                                restaurants.length > 0
                              ) {
                                handleSelectedRestaurantsHasError(false);
                              } else {
                                handleSelectedRestaurantsHasError(true);
                              }
                              setSelectedRestaurants(restaurants);
                            }}
                          />
                        </div>
                        {selectedRestaurantsHasError ? (
                          <div className="error-message">
                            {SELECT_RESTAURANT}
                          </div>
                        ) : (
                          ''
                        )}
                      </div>
                    ) : (
                      ''
                    )}
                  </div>
                </div>
              </div>
            </div>
            <form runat="server">
              <h4>Upload photo</h4>
              <input
                type="file"
                id="imgInp"
                accept="image/x-png ,image/png, image/jpeg, images/gif"
                onChange={e => getImage(e)}
              />
              <img
                id="blah"
                src={
                  // eslint-disable-next-line no-nested-ternary
                  photoURL !== '' ? photoURL : photo || ''
                }
                alt=""
                width="30%"
                style={{ width: '15%', marginTop: '10px' }}
              />
            </form>
            <div className="error-message">{errorMsgAddPromoCode}</div>
            <div className="text-center">
              <button
                disabled={isDisable}
                type="submit"
                className="btn-primary btn"
              >
                Save
              </button>
              <button
                type="button"
                disabled={isDisable}
                className="btn-primary btn m-l-sm"
                onClick={() => props.history.goBack()}
              >
                Cancel
              </button>
            </div>
          </form>
        )}
      </Formik>
      {open && <AlertMessageModal />}
    </CardWithBack>
  );
};

export default SetTokenHeader(withRouter(AddPromocode), axios);
